<template>
  <div class="counter">
      <hr>
    <h1>{{ msg }}</h1>
    <input type="button" value="增加" v-on:click="add">
    <input type="button" value="减少">
   <input type="text" v-model="this.$store.state.counter">
  </div>
</template>

<script>
export default {
  name: 'counter',
  data () {
    return {
      msg: 'counter pages'
    }
   
  },
   methods:{
        add(){
            this.$store.commit("increment")
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button{
    height: 30px
}
</style>



