<template>
  <div class="shop">
    <hr>
    <!-- <h1>当前数量：{{ msg }}</h1> -->
    <span>购物车的数量：</span>
    <input type="text" v-model="this.$store.state.counter">
  </div>
</template>

<script>
export default {
  name: 'shop',
  data () {
    return {
      msg: 5
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button{
    height: 30px
}
</style>



